/*
* 
*
* // 
* this.$toast.close();
*
* // 
* this.$toast.loading(string);
*
* // 
* this.$toast({
*    type: String,
*    message: String,
*    duration: 2500,
*    animate: Number, // [1, 2, 3]
*    onClose: function() {}
* });
*
* 
* type: ['success', 'warning', 'loading']
* duration: msdefault 2500, 0 
* message: 
* animate:  [1, 2, 3]
* onClose: 
*
*/

import Vue from 'vue';
import Main from './main.vue';

//  Vue 
let ToastConstructor = Vue.extend(Main);
let instance = null;
let instances = [];
let instanceOnClose = null; // 

const Toast = function(options) {
  // 
  if(instances.length !== 0) {
    instance.vm.$destroy();
    document.body.removeChild(instance.vm.$el);
  }

  // close 
  instanceOnClose = options.onClose;
  options.onClose = function() {
    Toast.close()
  }

  // 
  instance = new ToastConstructor({
    data: options
  });

  // 
  instance.vm = instance.$mount();
  document.body.appendChild(instance.vm.$el);
  instance.vm.visible = true;

  instances.push(instance)
  return instance.vm;
};

//  message
['success', 'warning', 'loading', 'info'].forEach(type => {
  Toast[type] = options => {
    if(typeof options === 'string') {
      options = {
        message: options
      };
    }
    options.type = type;
    return Toast(options);
  };
});

Toast.close = function() {
  if(instances.length !== 0) {
    if(instanceOnClose) {
      instanceOnClose();
    }
    instance.vm.$destroy();
    document.body.removeChild(instance.vm.$el);
    // reset
    instances.length = 0;
  }
};

export default Toast;
