// 

const _throttle  = function(func, wait, options) {
    var context, args, result;

    // setTimeout  handler
    var timeout = null;

    // 
    // 
    var previous = 0;

    //  options 
    //  options 
    if (!options) {
        options = {};
    }

    var later = function () {
        //  options.leading === false
        //  previous  0
        // 
        previous = options.leading === false ? 0 : new Date();
        timeout = null;
        // console.log('B')
        result = func.apply(context, args);

        //  timeout  null 
        // 
        if (!timeout) {
            context = args = null;
        }
    };

    // scroll
    // 
    // _.throttle 
    return function () {
        // 
        var now = new Date();

        //  previous  0 previous 
        // options.leading === false
        //  previous  now 
        // 
        if (!previous && options.leading === false) {
            previous = now;
        }

        //  func 
        var remaining = wait - (now - previous);
        context = this;
        args = arguments;

        // remaining <= 0
        //  {leading: false}
        //  previous  0wait - (now - previous)  <= 0
        //  previous  now
        // ========= //
        // remaining > wait
        //  func 
        // @see https://blog.coding.net/blog/the-difference-between-throttle-and-debounce-in-underscorejs
        // ========= //

        // console.log(remaining) 
        if (remaining <= 0 || remaining > wait) {
            if (timeout) {
                clearTimeout(timeout);
                // 
                timeout = null;
            }

            // 
            previous = now;

            // 
            // result 
            // console.log('A')
            result = func.apply(context, args);

            // 
            //  timeout  null  if 
            if (!timeout) {
                context = args = null;
            }

        } else if (!timeout && options.trailing !== false) { // 
            //  if 
            //  {trailing: false}
            //  remaining milliseconds  later 
            timeout = setTimeout(later, remaining);
        }

        // 
        return result;
    };
};

export default _throttle